<style>
  .p-m-w {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
  }
  .img-w {
    width: 80px;
    height: 80px;
  }
  .img-w>img {
    width: 80px;
  }
  .p-m-c-r {
    padding: 0 10px;
  }
  .erp-p-no {
    color: #666;
    font-size: 12px;
  }
</style>

<template>
  <div>
    <!-- 搜索框 -->
    <div class="search-form-wraper">
      <el-form :model="searchContent" label-position="top">
        <el-form-item label="所属门店">
          <el-select class="search-input" v-model="searchContent.storeNo" filterable placeholder="请选择所属门店" clearable size="small">
            <el-option
              v-for="item in storeOptions"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="下单时间">
          <el-time-picker
            is-range
            class="search-date-picker-range"
            v-model="searchContent.makeOrderTimeTemp"
            range-separator="-"
            start-placeholder="开始时间"
            size="small"
            format="HH:mm:ss"
            value-format="HH:mm:ss"
            end-placeholder="结束时间">
          </el-time-picker>
        </el-form-item>

        <el-form-item>
          <el-button type="primary" @click="doAllSearchHandel(true)" size="mini">搜索</el-button>
          <el-button size="mini" @click="exportData" :loading="exportLoading">导出</el-button>
        </el-form-item>
      </el-form>
    </div>
      
    <div class="table-wraper">
      <div class="total-num-w">
        合计：订单数 {{orderTotal.orderCount}} 笔；商品品种 {{orderTotal.productCount}} 个； 销售金额 ￥{{orderTotal.totalAmount}} 元
      </div>
      <el-table
        v-loading="loading"
        class="fixed-height"
        :data="tableData"
        empty-text="没有查询到您想要的数据"
        style="width:100%;"
        size="small">
        <el-table-column prop="productNo" label="商品信息" min-width="236px">
          <template slot-scope="scope">
            <div class="p-m-w">
              <div class="img-w">
                <img v-if="scope.row.imgUrl && scope.row.imgUrl != ''" :src="scope.row.imgUrl" alt="" onerror="onerror=null;src='/shop/static/img_error.png'">
                <img v-else src="/shop/static/img_error.png" alt="">
              </div>
              <div class="p-m-c-r">
                <div>{{scope.row.platformCityGoodsName}}</div>
                <div class="erp-p-no">SKU编码：{{scope.row.goodsNo || '--'}}</div>
                <div class="erp-p-no">预售商品ID：{{scope.row.platformCityGoodsId}}</div>
              </div>
            </div>
          </template>        
        </el-table-column>
        <el-table-column min-width="120px" prop="cityName" label="预售区域"></el-table-column>
        <el-table-column min-width="120px" prop="preSalePrice" label="当前预售价"></el-table-column>
        <el-table-column min-width="120px" prop="qty" label="预售数量"></el-table-column>
        <el-table-column min-width="120px" prop="productAmount" label="合计金额"></el-table-column>
        <el-table-column min-width="120px" prop="orderCount" label="订单数"></el-table-column>
        
      </el-table>
      <!-- 分页条 -->
      <el-pagination
        v-show="tableData.length>0"
        class="tc mt20"
        background
        @size-change="changeSize"
        @current-change="changeCurrent"
        :current-page.sync="pageNum"
        :page-sizes="[10, 50, 100, 150, 200]"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </div>

  </div>
</template>

<script>
  import * as utils from '@/utils/utils'
  import exportSheet from '@/utils/exportSheet'
  import Storage from '@/utils/localStorage'
  const orderLiftUrl = {
    // 获取门店列表接口
    selectStore: '/plateform/a/store/condition/selectStore',
    // 【订单管理】—明日自提合计
    tomorrowTotal: '/plateform/a/mall/orderProduct/tomorrowTotal',
    // 【订单管理】—明日自提商品明细
    tomorrow: '/plateform/a/mall/orderProduct/tomorrow'
  }
  export default {
    data() {
      return {
        exportLoading: false,
        loading: false,
        pageNum: 1,
        pageSize: 10,
        total: 0,
        searchContent: {
          regionCode: Storage.get('regionCode') || '',
          storeNo: '', // 门店编号
          makeOrderTimeTemp: ['', ''],
          startDateTime: '', // 下单开始时间，格式：yyyy-MM-dd HH:mm:ss,不传默认为当前日期+1天 凌晨：00:00:00
          endDateTime: '' // 下单开始时间，格式：yyyy-MM-dd HH:mm:ss，不传默认为当前日期+1天晚上：23:59:59
        },
        // 门店列表下拉框
        storeOptions: [],
        tableData: [],
        orderTotal: {
          orderCount: 0,
          orderCount: 0,
          orderCount: 0.00
        },
        exportConfig: {
          productName: '商品名称',
          goodsNo: 'SKU编码',
          productNo: 'ERP编码',
          cityName: '预售区域',
          preSalePrice: '当前预售价',
          qty: '预售数量',
          productAmount: '合计金额',
          orderCount: '订单数'
        }

      }
    },
    created() {
      // 获取门店
      this.getStoreList()
    },
    mounted() {
      // 获取表格中和统计的数据
      this.doAllSearchHandel()
    },
    methods: {
      /**
       * [getStoreList 获取门店列表]
       * @return {[type]} [description]
       */
      getStoreList() {
        this.$http.post(orderLiftUrl.selectStore, {DIFFCITY: true}).then(res => {
          if(res.code === 0) {
            if(res.data && res.data.length) {
              let optionsArr = []
              for(let item of res.data) {
                let optionItem = {
                  value: item.erpStoreNo,
                  label: item.erpStoreNo +'-'+ item.erpStoreName
                }
                optionsArr.push(optionItem)
              }
              this.storeOptions = optionsArr
            }
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          console.log("提示(获取门店列表):", e.message)
        })
      },
      /**
       * [changeSize description]
       * @param    {[type]}                 value [description]
       * @return   {[type]}                       [description]
       */
      changeSize (value) {
        this.pageSize = value
        this.doSearchHandel()
      },
      /**
       * [doAllSearchHandel 获取表格中和统计的数据]
       * @return {[type]} [description]
       */
      doAllSearchHandel(_param) {
        // 获取表格数据
        this.doSearchHandel(_param)
        // 【订单管理】—明日自提合计
        this.getTomorrowTotal()
      },
      /**
       * [changeCurrent 点击分页条]
       * @param    {[type]}                 value [当前页]
       * @return   {[type]}                       [description]
       */
      changeCurrent (value) {
        this.pageNum = value
        this.doSearchHandel()
      },
      maketSearchTime() {
        let tommorrowTimeStamp = Date.parse(new Date())
        if(this.searchContent.makeOrderTimeTemp) {
          if(this.searchContent.makeOrderTimeTemp[0] && this.searchContent.makeOrderTimeTemp[0] != '' || this.searchContent.makeOrderTimeTemp[1] && this.searchContent.makeOrderTimeTemp[1] != '') {
            this.searchContent.startDateTime = utils.timestampToTimeNoHMS(tommorrowTimeStamp) + ' ' + this.searchContent.makeOrderTimeTemp[0]
            this.searchContent.endDateTime = utils.timestampToTimeNoHMS(tommorrowTimeStamp) + ' ' + this.searchContent.makeOrderTimeTemp[1]
          }else {
            this.searchContent.startDateTime = ''
            this.searchContent.endDateTime = ''
          }
        }else {
          this.searchContent.makeOrderTimeTemp = ['', '']
          this.searchContent.startDateTime = ''
          this.searchContent.endDateTime = ''
        }
      },
      /**
       * [doSearchHandel 获取表格数据]
       * @param  {[type]} _param [description]
       * @return {[type]}        [description]
       */
      doSearchHandel(_param) {
        this.loading = true
        if(_param === true) {
          this.pageNum = 1
        }
        // 处理开始时间和结束时间
        this.maketSearchTime()      
        this.$http.post(orderLiftUrl.tomorrow, {...this.searchContent, pageNum: this.pageNum, pageSize: this.pageSize, DIFFCITY: true }).then(res => {
          this.loading = false
          if(res.code === 0) {
            if(res.data && res.data.list && res.data.list.length) {
              this.tableData = res.data.list
            }else {
              this.tableData = []
            }

            if(this.pageNum == 1) {
              this.total = res.data.total
            }

            return 
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.loading = false
          this.$notify.error({
            title: '提示(明日自提商品明细):',
            message: e.message
          })
        })
      },
      /**
       * [getTomorrowTotal 【订单管理】—明日自提合计]
       * @return {[type]} [description]
       */
      getTomorrowTotal() {
        // 处理开始时间和结束时间
        this.maketSearchTime() 

        this.$http.post(orderLiftUrl.tomorrowTotal, {...this.searchContent, DIFFCITY: true}).then(res => {
          if(res.code === 0) {
            this.orderTotal = res.data
            return 
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.$notify.error({
            title: '提示(明日自提合计)',
            message: e.message
          })
        })
      },
      /**
       * [exportData 导出数据]
       * @return {[type]} [description]
       */
      exportData() {
        // 处理开始时间和结束时间
        this.maketSearchTime()      
        this.$http.post(orderLiftUrl.tomorrow, {...this.searchContent, DIFFCITY: true}).then(res => {
          this.loading = false
          if(res.code === 0) {
            if(res.data && res.data.list && res.data.list.length) {
              let tableDataArr = res.data.list
              exportSheet.exportFileByConfig(tableDataArr, '明日自提商品明细.xlsx', this.exportConfig)
              setTimeout(() => {
                this.$message.success("导出成功")
              }, 500)
            }else {
              this.$notify.warning({
                title: '温馨提示',
                message: '没有查询到要导出的数据'
              });
            }
            return 
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.$notify.error({
            title: '提示(导出数据)：',
            message: e.message
          })
        })
      }


    }

  }
</script>


